  .container {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .user-info {
    display: flex;
    float: left;
    flex-direction: column;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    width: 495px;
    height: 355px;
  }
  
  .user-info__row {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .user-info__icon {
    margin-right: 10px;
    font-size: 20px;
    margin-left: 30px;
  }

  .user-info__email {
    font-size: 14px;
    color: #999;
    margin-left: 10px;
    margin-left: 30px;
  }
  
  .user-info__value {
    flex-grow: 1;
  }

  .user-info__avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    overflow: hidden;
    margin-right: 10px;
    margin-left: 30px;
  }
  
  .user-info__avatar img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  #userinput{
    border: 1px #000000 solid;
    margin-left: 30px;
    margin-bottom: 20px;
    width: 400px;
  }

  .calendarApp{
    float: right;
  }

  @import url("https://use.fontawesome.com/releases/v5.15.3/css/all.css");

  .github-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    background-color: #24292e;
    color: #fff;
    border-radius: 50%;
    font-size: 20px;
    margin-left: 30px;
  }

  .naver-blog-icon{
    background: url(../img/naver.png) no-repeat center center; background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    margin-left: 30px;
    margin-right: 10px;
  }

  .user-info__row button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px;
    border-radius: 5px;
    margin-right: 10px;
  }

  .user-info-text{
    text-align: center;
  }

  .user-info-link{
    margin-left: 55px;
  }
  
  .user-info__row button:hover {
    background-color: #0069d9;
    cursor: pointer;
  }

  .user-info_Ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .user-info_li {
    margin-bottom: 5px;
  }
  
  .user-info_a {
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }